<template>
  <div>
    <h1>
      <a href="/" id="link">Home</a>
    </h1>
    <h2>
      Approve Loan
    </h2>
    <hr>
    <br><br>
    <b-form @submit="onSubmit" @reset="onReset" v-if="show">
      <b-form-group
        id="input-group-1"
        label="Candidate Age:"
        label-for="input-1"
      >
        <b-form-input
          id="input-1"
          v-model="form.age"
          placeholder="Enter age"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-2" label="Candidate job:" label-for="input-2">
        <b-form-input
          id="input-2"
          v-model="form.job"
          placeholder="Enter job"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-3" label="Candidate marital:" label-for="input-3">
        <b-form-input
          id="input-3"
          v-model="form.marital"
          placeholder="Enter marital"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-4" label="Candidate education:" label-for="input-4">
        <b-form-input
          id="input-4"
          v-model="form.education"
          placeholder="Enter education"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-5" label="Candidate default:" label-for="input-5">
        <b-form-input
          id="input-5"
          v-model="form.default"
          placeholder="Enter default"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-6" label="Candidate balance:" label-for="input-6">
        <b-form-input
          id="input-6"
          v-model="form.balance"
          placeholder="Enter balance"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-7" label="Candidate housing:" label-for="input-7">
        <b-form-input
          id="input-7"
          v-model="form.housing"
          placeholder="Enter housing"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-8" label="Candidate loan:" label-for="input-8">
        <b-form-input
          id="input-8"
          v-model="form.loan"
          placeholder="Enter loan"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-9" label="Candidate contact:" label-for="input-9">
        <b-form-input
          id="input-9"
          v-model="form.contact"
          placeholder="Enter contact"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-10" label="Candidate day:" label-for="input-10">
        <b-form-input
          id="input-10"
          v-model="form.day"
          placeholder="Enter day"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-11" label="Candidate month:" label-for="input-11">
        <b-form-input
          id="input-11"
          v-model="form.month"
          placeholder="Enter month"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-12" label="Candidate duration:" label-for="input-12">
        <b-form-input
          id="input-12"
          v-model="form.duration"
          placeholder="Enter duration"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-13" label="Candidate campaign:" label-for="input-13">
        <b-form-input
          id="input-13"
          v-model="form.campaign"
          placeholder="Enter campaign"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-14" label="Candidate pdays:" label-for="input-14">
        <b-form-input
          id="input-14"
          v-model="form.pdays"
          placeholder="Enter pdays"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-15" label="Candidate previous:" label-for="input-15">
        <b-form-input
          id="input-15"
          v-model="form.previous"
          placeholder="Enter previous"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group id="input-group-16" label="Candidate poutcome:" label-for="input-16">
        <b-form-input
          id="input-16"
          v-model="form.poutcome"
          placeholder="Enter poutcome"
          required
        ></b-form-input>
      </b-form-group>

      <b-button type="submit" variant="primary">Submit</b-button>
      <b-button type="reset" variant="danger">Reset</b-button>
    </b-form>
    <b-card class="mt-3" header="Form Data Result">
      <pre class="m-0">{{ form }}</pre>
    </b-card>
    <table class="table table-hover">
          <thead>
          <tr>
            <th scope="col">ID</th>
            <th scope="col">Age</th>
            <th scope="col">Balance</th>
            <th scope="col">Is Approved</th>
            <th scope="col">Creation Timestamp</th>
            <th></th>
          </tr>
          </thead>
          <tbody>
          <tr v-for="user in users" :key="user.id">
            <td>{{ user.id }}</td>
            <td>{{ user.age }}</td>
            <td>{{ user.balance }}</td>
            <td>{{ user.is_approved }}</td>
            <td>{{ user.created_ts }}</td>
          </tr>
          </tbody>
        </table>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  data () {
    return {
      users: [],
      form: {
        age: '',
        job: '',
        marital: '',
        education: '',
        default: '',
        balance: '',
        housing: '',
        loan: '',
        contact: '',
        day: '',
        month: '',
        duration: '',
        campaign: '',
        pdays: '',
        previous: '',
        poutcome: ''
      },
      show: true,
      path: 'http://localhost:8002/'
    }
  },
  methods: {
    onSubmit (event) {
      event.preventDefault()
      const payload = {
        age: this.form.age,
        job: this.form.job,
        marital: this.form.marital,
        education: this.form.education,
        default: this.form.default,
        balance: this.form.balance,
        housing: this.form.housing,
        loan: this.form.loan,
        contact: this.form.contact,
        day: this.form.day,
        month: this.form.month,
        duration: this.form.duration,
        campaign: this.form.campaign,
        pdays: this.form.pdays,
        previous: this.form.previous,
        poutcome: this.form.poutcome
      }
      console.log(payload)
      const path = this.path + 'predict/'
      axios.post(path, payload)
        .then((response) => {
          console.log(response)
          this.getUsers()
        })
        .catch((error) => {
          console.log(error)
        })
    },
    onReset (event) {
      event.preventDefault()
      // Reset our form values
      this.form.age = ''
      this.form.job = ''
      this.form.marital = ''
      this.form.education = ''
      this.form.default = ''
      this.form.balance = ''
      this.form.housing = ''
      this.form.loan = ''
      this.form.contact = ''
      this.form.day = ''
      this.form.month = ''
      this.form.duration = ''
      this.form.campaign = ''
      this.form.pdays = ''
      this.form.previous = ''
      this.form.poutcome = ''
      // Trick to reset/clear native browser form validation state
      this.show = false
      this.$nextTick(() => {
        this.show = true
      })
    },
    getUsers () {
      const path = this.path + 'users'
      axios.get(path)
        .then((res) => {
          this.users = res.data.users
          console.log(res.data)
        })
        .catch((error) => {
          console.error(error)
        })
    }
  },
  created () {
    this.getUsers()
  }
}
</script>

<style scoped>

div {
  font-weight: normal;
  margin: 50px;
}

button {
  font-weight: normal;
  margin: 50px;
}

#link {
  color: #28a745;
}

</style>



