<template>
  <div class="container">
    <div class="row">
      <div class="col-sm-10">
        <h1>
          <a href="/" id="link">Home</a>
        </h1>
        <h2>
          Metrics
        </h2>
        <hr><br><br>
<!--        <alert :message=message></alert>-->
        <br><br>
        <div class="metric-block" id="total-count">
          <h2>
            Total count of all Applicants: {{ totalCount }}
          </h2>
        </div>
        <div class="metric-block" id="total-count-approved">
          <h2>
            Total count of all Approved Applicants: {{ totalCountApproved }}
          </h2>
        </div>
        <div class="metric-block" id="total-count-rejected">
          <h2>
            Total count of all Rejected Applicants: {{ totalCountRejected }}
          </h2>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

// axios.defaults.headers.common['Access-Control-Allow-Origin'] = '*'
export default {
  data () {
    return {
      totalCount: 0,
      totalCountApproved: 0,
      totalCountRejected: 0,
      path: 'http://localhost:8002/'
    }
  },
  methods: {
    getTotalCount () {
      const path = this.path + 'users'
      axios.get(path)
        .then((res) => {
          this.totalCount = res.data.users.length
          console.log(res.data)
        })
        .catch((error) => {
          console.error(error)
        })
    },
    getTotalCountApproved () {
      const path = this.path + 'users'
      axios.get(path)
        .then((res) => {
          this.totalCountApproved = res.data.users
            .filter(user => user.is_approved === 'Yes')
            .length
          console.log(res.data)
        })
        .catch((error) => {
          console.error(error)
        })
    },
    getTotalCountRejected () {
      const path = this.path + 'users'
      axios.get(path)
        .then((res) => {
          this.totalCountRejected = res.data.users
            .filter(user => user.is_approved === 'No')
            .length
          console.log(res.data)
        })
        .catch((error) => {
          console.error(error)
        })
    }
  },
  created () {
    this.getTotalCount()
    this.getTotalCountApproved()
    this.getTotalCountRejected()
  }
}
</script>

<style scoped>

div {
  font-weight: normal;
  margin: 50px;
}

.metric-block {
  margin: 10px;
  background: #28a745;
  padding: 30px;
  border: dotted lightslategrey;
}

#link {
  color: #28a745;
}
</style>



